.navbar {
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
  }
  
  .navbar ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  .navbar ul li {
    display: inline;
    margin-right: 20px;
  }
  
  .navbar ul li:last-child {
    margin-right: 0;
  }
  
  .navbar ul li a {
    text-decoration: none;
    color: #fff;
  }
  
  .navbar ul li a:hover {
    color: #ffd700; /* Change color on hover */
  }
  